* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.title {
  margin: 1.5em;
  font-weight: 600;
  text-transform: uppercase;
}
.blogContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 98.92vw;
  height: 100vh;
  font-family: Fira Sans, sans-serif;
  color: #454444;
}
.blogImage {
  width: 50%;
  height: 60vh;
  border-radius: 5px;
  object-fit: cover;
  -webkit-box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
}
.content {
  margin-top: 5em;
  width: 80%;
  font-size: 1.3em;
  text-align: justify;
  text-justify: inter-word;
}
.comment {
  margin-top: 3em;
  width: 80%;
  display: flex;
  flex-direction: column;
  font-family: Fira Sans, sans-serif;
}
.comment textarea {
  margin: 1em 0;
  height: 6em;
  border-radius: 3px;
  border: 2px solid #deba3b;
  border-color: #deba3b;
  font-size: 1.3em;
}
.comment textarea:focus {
  outline: none !important;
  border: 1px solid #deba3b;
}
.comment button {
  width: 120px;
  align-self: flex-end;
  margin-bottom: 5em;
  border-radius: 25px;
  height: 3em;
  font-size: 0.8em;
  border: none !important;
  background: #deba3b;
  color: #454444;
  font-family: Fira Sans, sans-serif;
  -webkit-box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 4px 4px 5px 0px rgba(50, 50, 50, 0.75);
  transition: all 0.2s ease-in-out;
}
.comment button:hover {
  transform: scale(1.1);
}
